import React, { useReducer } from 'react'
import { Link, Switch, Route, useRouteMatch } from './routing';
import { View, TouchableOpacity, Text, Button } from 'react-native';
import { FontAwesomeIcon } from '@fortawesome/react-native-fontawesome'
import { faQuestionCircle } from '@fortawesome/free-solid-svg-icons'
import Radio from './Radio.js';
import RadioButton from './RadioButton.js';
import GrammarTestReducer from './GrammarTestReducer.js';
import GrammarTestCorrectAnswer from './GrammarTestCorrectAnswer.js';
import GrammarTestWrongAnswer from './GrammarTestWrongAnswer.js';
import GrammarExplanation from './GrammarExplanation.js';
import BaseContext from './BaseContext.js'
import { buildAnswers } from './Answers.js'
import Styles from './Styles.js'

let initialState = buildAnswers();

const GrammarTestQuestion = props => {
  return (
    <BaseContext.Consumer>
      {({state, dispatch}) => (
          <React.Fragment>
            <View style={Styles.grammarTestCasePromptContainer}>
              <Text style={Styles.grammarTestCasePromptText}>{state.prompt} <FontAwesomeIcon onPress={ () => dispatch({ type: 'SHOW_GRAMMAR_HELP' })} size={24} color="#888888" icon={faQuestionCircle} /></Text>
              <Text style={Styles.grammarTestCaseTranslationText}>{state.translation}</Text>
            </View>
            <View style={Styles.grammarTestCaseContainer}>
              <Radio>
                {state.allAnswers.map((answer) =>
                  <RadioButton key={answer} title={answer} id={answer} onPress={ () => dispatch(
                    { type: 'SELECT_ANSWER', answer: answer }
                  )} />
                )}
              </Radio>
            </View> 
          </React.Fragment>
        )
      }
    </BaseContext.Consumer>
  )
}

const GrammarTestCase = props => {
  const [state, dispatch] = useReducer(GrammarTestReducer, initialState)

  let child = {}

  if(state.showGrammarHelp){
    child = <GrammarExplanation />
  }
  else if(state.checkAnswers){
    if(state.selectedAnswer == state.correctAnswer){
      child = <GrammarTestCorrectAnswer />
    }
    else{
      child = <GrammarTestWrongAnswer />
    }
  }
  else{
    child = <GrammarTestQuestion state={state} dispatch={dispatch} />
  }

  return (
    <BaseContext.Provider value={{state: state, dispatch: dispatch}}>
      {child}
    </BaseContext.Provider>
  )
}
export default GrammarTestCase
