import React from 'react'
import { View, TouchableOpacity, Text, Button } from 'react-native'
import { FontAwesomeIcon } from '@fortawesome/react-native-fontawesome'
import { faQuestionCircle } from '@fortawesome/free-solid-svg-icons'
import BaseContext from './BaseContext.js'
import Styles from './Styles.js'

const GrammarTestWrongAnswer = (props) => {
  return (
    <BaseContext.Consumer>
      {({state, dispatch}) => (
          <React.Fragment>
            <View style={Styles.grammarTestExplanationHeader}>
              <Text style={Styles.grammarTestCasePromptText}>Wrong</Text>
            </View>
            <View style={Styles.grammarTestExplanationBody}>
              <Text style={Styles.grammarTestAnswerExplanationText}>You answered {state.selectedAnswer}</Text>
              <Text style={Styles.grammarTestAnswerExplanationText}>The correct answer was {state.correctAnswer}</Text>
              <Text style={Styles.grammarTestAnswerExplanationText}>"{state.correctSentence}"</Text>
              <Text style={Styles.grammarTestAnswerExplanationText}>{state.explanation} case <FontAwesomeIcon onPress={ () => dispatch({ type: 'SHOW_GRAMMAR_HELP' })} size={12} color="#888888" icon={faQuestionCircle} /></Text>
            </View>
            <View style={Styles.grammarTestCaseContainer}>
              <Button title="Next question" onPress={ () => dispatch({type: 'REFRESH'})} />
            </View>
          </React.Fragment>
        )
      }
    </BaseContext.Consumer>
  )
}

export default GrammarTestWrongAnswer